header {
  position: fixed;
  width: 100%;
  height: 120px;
  background: #333;
  color: white;
  padding: 1rem;
  text-align: center;
  z-index: 2;
}

header nav a {
  color: white;
  margin: 0 1rem;
  text-decoration: none;
}

section {
  padding: 120px 0 50px 0;
  border-bottom: 1px solid #ccc;
}

h2 {
  margin-bottom: 0.5rem;
}
